    @media (max-width: 1024px) {
      .animation {
        overflow:hidden;
            margin-left: 10rem; 
            height: 2rem;
            margin-top: 10px;
            font-size: 2rem;
          }
      .text_center{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        
      }
      
      #Cardslide1 p ,#Cardslide p {
        color: white;
        margin-top: 1px;
        font-size: 18px;
        font-weight: normal;
        line-height: 1.2; 
    }
    #slider,#slider1{
      width: 100%;
    }
    #Cardslide ,#Cardslide1 {
      width: 10.5rem;
      height: 8rem;
      padding: 10px;
    }
  }
  @media (min-width: 768px) and (max-width:1024px){
  
    #Cardslide ,#Cardslide1 {
      width: 20rem;
      height: 8rem;
      padding: 10px;
    }
    #Cardslide1 p ,#Cardslide p {
      color: white;
      margin-top: 1px;
      font-size: 23px;
      font-weight: normal;
      line-height: 1.2; 
  }
  
  
  
  
  
  }